import { div, input, i, button, span, find, findOne } from "./framework";

let rootElement = {};
const url = !process.env.DEBUG ? "https://us-central1-just-software.cloudfunctions.net/just-api/subscribe":"http://localhost:8092/subscribe";
const justProtect = !process.env.DEBUG ? "https://just-software-tech.github.io/just-protect-ui/bundle.js": "http://localhost:8081/bundle.js";
let clientId = "";
let clientKey = ""

global.gotValue = async (v) => {
    let subscribe = document.getElementById("subscribe")
  
  const result = await fetch(url,{
    method:"POST",
    headers:{
        "clientId":clientId,
        "clientKey": clientKey,
        "Content-Type": "application/json"
    },
    body:JSON.stringify({
        "value":subscribe.value,
        "nobotkey":v.key,
        "nobotvalue":v.val
    })
  })
  .then((m)=>{
    
    if(m.ok){
        let overlay = findOne(rootElement,".overlay")
        findOne(rootElement,".overlay").textContent = ""

        const successMessage = div({
            class:"successMessage"
        },[
            i({class:"fa fa-check"},[]),
            span({},[],"you are subscribed")
        ])
        overlay.appendChild(successMessage)
    }else{
        //findOne(rootElement,".overlay").textContent = "an error occured"
        let overlay = findOne(rootElement,".overlay")
        let errorMessage = findOne(overlay,".errorMessage")
        if(!errorMessage){
            errorMessage = div({
            class:"errorMessage"
        },[
            i({class:"fa fa-exclamation-triangle"},[]),
            span({},[],"invalid combination")
        ])
        overlay.appendChild(errorMessage)
    }
    }
    return m
  })
  .catch((ex)=>{
    
    findOne(rootElement,".overlay").textContent = "an error occured";return null}
    )
  
  
  
};

export const validEmail = (e) => {
    var filter = /^\s*[\w\-\+_]+(\.[\w\-\+_]+)*\@[\w\-\+_]+\.[\w\-\+_]+(\.[\w\-\+_]+)*\s*$/;
    return String(e).search (filter) != -1;
}

export const subscribeClick = (e) => {
  
  let subscribe = document.getElementById("subscribe")
  
  if(subscribe.value.trim() === "" || !validEmail(subscribe.value.trim()))
  {
    subscribe.classList.add("error")
    return
  }

  
  let over = div(
    {
      class: "overlay",
    },
    [span({ class: "loader" }, [])]
  );
  rootElement.appendChild(over);
  rootElement.classList.add("active")

  const overlay = findOne(rootElement, ".overlay");
  overlay.textContent = "";

  

  
  let nobot = document.getElementById("nobot");
  while(nobot){
    
    nobot.parentNode.removeChild(nobot)
    nobot = document.getElementById("nobot");
  }
  
  let nobotholder = div(
    {
      id: "nobot",
      "data-call": "gotValue(val)",
      clientId: clientId,
      clientKey: clientKey,
      salt: subscribe.value
    },
    []
  );
  overlay.appendChild(nobotholder);

  let include = document.createElement("script");
  include.src = justProtect;
  findOne(document, "head").appendChild(include);

};

export const setUp = (el) => {
  rootElement = el;

  clientId = el.getAttribute("clientId")
  clientKey = el.getAttribute("clientKey")

  let d = div({}, [
    input(
      {
        placeholder: "email",
        id:"subscribe"
      },
      []
    ),
    button(
      {
        click: subscribeClick,
        class:"writeButton"
      },
      [],
      "Subscribe"
    ),
  ]);
  el.appendChild(d);
};
